










































































import Vue from "vue";
import { Icons } from "../../assets/images";

export default Vue.extend({
  name: "NavBar",
  data() {
    return {
      showBurgerMenu: false,
      homePageIcon: Icons.homePage,
      linkedinIcon: Icons.linkedin,
      xingIcon: Icons.xing
    };
  },
  methods: {
    onBurgerItemClicked(event: any) {
      this.showBurgerMenu = false;
    }
  }
});
