<template>
  <div id="modal">
    <transition name="modal">
      <div class="modal-background" @click.self="onClosed">
        <div class="modal-window" v-bind:style="windowStyleOverride">
          <div class="modal-header">
            <a class="exit-button" @click="onClosed">&#10006;</a>
          </div>
          <div class="modal-content">
            <slot></slot>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "ModalWindow",
  props: {
    width: String,
    height: String
  },
  mounted() {
    // Disables the website's scroll when modal window is open.
    document.querySelector("html").style.overflow = "hidden";
  },
  beforeDestroy() {
    // Enables the website's scroll when modal window is closed.
    document.querySelector("html").style.overflow = "visible";
  },
  methods: {
    onClosed() {
      document.body;
      this.$emit("onClose");
    }
  },
  computed: {
    windowStyleOverride() {
      return {
        width: this.width,
        height: this.height
      };
    }
  }
};
</script>

<style scoped>
#modal {
  width: 100%;
  height: 100%;
  position: fixed; /* Follows when scrolling. */
  z-index: 9998; /* In front of everything. */
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  pointer-events: all;
}
/*
* A fullscreen background.
*/
.modal-background {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: column;
  transition: opacity 0.3s ease;
}
/*
* The modal window that contains everything.
*/
.modal-window {
  align-self: center;
  height: 80%;
  width: 80%;
  align-self: center;
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: column;
  background-color: rgb(206, 206, 206);
  padding: 3% 2%;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
}
/*
* Header and Exit button.
*/
.modal-window .modal-header {
  position: relative;
  width: 100%;
  height: auto;
  margin-bottom: 0rem;
  margin-top: 2rem;
}

.exit-button {
  float: right;
  vertical-align: middle;
  margin-right: 0.5rem;
}

a {
  color: black;
  background-color: #7c93ad;
  float: left;
  padding: 3px 9px;
  border-radius: 6px;
  text-decoration: none;
  transition: background-color 0.1s;
  border: 0.5px solid rgb(146, 146, 146);
}

a.disabled {
  background-color: rgb(202, 202, 202);
  opacity: 0.25;
  cursor: default;
}

a:hover {
  filter: brightness(120%);
}

a:active {
  filter: brightness(90%);
}

/*
* Content
*/
.modal-content {
  position: relative;
  height: 100%;
  width: 100%;
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans-serif;
  margin: 2%;
  overflow-y: auto;
}
/*
 * Visibility toggle transitions for modals.
 */

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-content,
.modal-leave-active .modal-content {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>
