// Importing the context images from relative path using a method described in
// https://stackoverflow.com/questions/42118296/dynamically-import-images-from-a-directory-using-webpack
// Accessed on 2021-11-27.
const importImagesFromFolder = (require: any) => {
  const images: any = {};
  require.keys().map((item: any) => {
    // Make the keys only consist of the image names.
    images[item.split("/").pop()] = require(item);
  });
  return images;
};

// Loads all images into context and return them without their folder paths.
const loadAllImages = () => {
  const images = importImagesFromFolder(
    require.context("./images/", true, /\.(png|jpe?g|svg)$/)
  );
  return images;
};

// Save all the images in a variable on load.
const images = loadAllImages();
// Allowed file types for images.
type ImageType = ".png" | ".jpg" | ".jpeg" | ".svg";

export const getImage = (
  imageName: string | null,
  imageType: ImageType = ".png"
): any => {
  if (imageName == null) {
    return null;
  }
  const imagePath = images[String(imageName + imageType)];
  return imagePath;
};

export const GenericImages = {
  VueLogo: getImage("logo"),
  ProfilePicture: getImage("profile-picture-256x256")
};

