














import Vue from "vue";
import { ProjectItemInfo } from "../ProjectItemInfo";
import { Icons } from "@/assets/images";
import ProjectRowContent from "./ProjectRowContent.vue";
import ModalWindow from "../../generic/ModalWindow.vue";

export default Vue.extend({
  name: "ProjectInfoWindow",
  props: {
    projectItemInfo: {
      type: ProjectItemInfo,
      required: true
    }
  },
  components: {
    ProjectRowContent,
    ModalWindow
  },
  data() {
    return {
      Icons: Icons
    };
  },
  methods: {
    onClosed() {
      this.$emit("onClose");
    }
  }
});
