















































































import Vue from "vue";
import { ProjectItemInfo } from "../ProjectItemInfo";
import { Icons } from "@/assets/images";
import LightBoxMedia from "../../generic/LightBoxMedia.vue";
import PointsList from "../../generic/PointsList.vue";

export default Vue.extend({
  name: "ProjectRowContent",
  props: {
    showExtended: {
      type: Boolean,
      required: true
    },
    projectItemInfo: {
      type: ProjectItemInfo,
      required: true
    }
  },
  components: {
    LightBoxMedia,
    PointsList
  },
  data() {
    return {
      Icons: Icons
    };
  },
  methods: {
    hasTitleMedia: function() {
      return (
        this.projectItemInfo.image !== null ||
        this.projectItemInfo.videoLink !== null
      );
    }
  }
});
