import { ProjectItemInfo } from "./ProjectItemInfo";
import { ProjectContent } from "@/assets/texts/json/project-page.json";
import { getImage } from "../../assets/images";
import { MediaItem } from "@/generics/Interfaces";

// Here additional information, not in the json-file is added to the media item array.
const addMissingMediaInformation = (
  mediaItems: MediaItem[] | null
): MediaItem[] | null => {
  if (mediaItems == null) {
    return null;
  }

  mediaItems.forEach((item: MediaItem) => {
    item.sourcePath = getImage(item.fileName);
  });

  return mediaItems;
};

export const getProjectItemsFromTitles = (
  projectTitles: string[]
): ProjectItemInfo[] | null => {
  if (projectTitles == null) return null;

  const returnArray: ProjectItemInfo[] = [];
  const projects = ProjectContent.Projects.filter(project =>
    projectTitles.includes(project.title)
  );

  projects.forEach((object: any) => {
    returnArray.push(
      new ProjectItemInfo(
        object.title,
        object.subtitle,
        object.summary,
        object.infoPoints,
        object.description,
        getImage(object.imageName),
        object.videoLink,
        addMissingMediaInformation(object.mediaItems),
        object.projectLink,
        object.reportLink,
        object.tags
      )
    );
  });

  return returnArray;
};

export const getAllProjectItems = (): ProjectItemInfo[] | null => {
  const projectTitles: string[] = [];
  ProjectContent.Projects.forEach((object: any) => {
    projectTitles.push(object.title);
  });

  return getProjectItemsFromTitles(projectTitles);
};
