

















































import Vue from "vue";
const CoolLightBox = require("vue-cool-lightbox").default;
import "vue-cool-lightbox/dist/vue-cool-lightbox.min.css";
import { MediaItem } from "@/generics/Interfaces";
import { FlexStyleProp } from "@/generics/Types";
import ImageWithCounterOverlay from "./ImageWithCounterOverlay.vue";

export default Vue.extend({
  name: "LightBoxMedia",
  components: {
    CoolLightBox,
    ImageWithCounterOverlay
  },
  props: {
    mediaItems: {
      type: Array as () => MediaItem[],
      required: true
    },
    // Props for flex styles to make component more flexible.
    flexStyles: {
      type: Object as () => FlexStyleProp,
      required: false,
      default: function(): FlexStyleProp {
        return {
          display: "flex",
          alignItems: "center",
          flexDirection: "row",
          justifyContent: "center",
          flexWrap: "wrap"
        };
      }
    },
    mediaPerRow: {
      type: Number,
      required: false,
      default: 5
    },
    numberOfRows: {
      type: Number,
      required: false,
      default: -1
    }
  },
  methods: {
    showImageOverlay: function(index: number): boolean {
      if (this.numberOfRows <= 0) {
        return false;
      }

      if (index < this.mediaPerRow * this.numberOfRows - 1) {
        return false;
      }
      return true;
    }
  },
  computed: {
    mediaStyle() {
      const percentageMargin = 1;
      const mediaPerRow = Math.min(this.mediaItems.length, this.mediaPerRow);
      const widthPercent = 100 / mediaPerRow - percentageMargin * 2;
      return { width: widthPercent + "%", margin: percentageMargin + "%" };
    },
    // Adapting the images to the CoolLightBox input.
    getConvertedMediaItems: function(): object[] {
      const coolItems: any = [];

      this.mediaItems.forEach((item: MediaItem) => {
        coolItems.push({
          title: item.title,
          description: item.description,
          src: item.sourcePath,
          alt: item.altText
        });
      });

      return coolItems;
    },
    // If a specified number of rows the shown media
    // items are less than the total media items.
    shownMediaItems: function(): MediaItem[] {
      if (this.numberOfRows > 0) {
        return this.mediaItems.slice(0, this.mediaPerRow * this.numberOfRows);
      }

      return this.mediaItems;
    }
  },
  data: function() {
    return {
      index: null
    };
  }
});
