import { MediaItem } from "@/generics/Interfaces";

export class ProjectItemInfo {
  public title: string;
  public subtitle: string | null;
  public summary: string;
  public infoPoints: string[] | null;
  public description: string | null;
  public image: string | null;
  public videoLink: string | null;
  public mediaItems: MediaItem[] | null;
  public projectLink: string | null;
  public reportLink: string | null;
  public tags: string[] | null;

  public constructor(
    title: string,
    subtitle: string | null,
    summary: string,
    infoPoints: string[] | null,
    description: string | null,
    image: string | null,
    videoLink: string | null,
    mediaItems: MediaItem[] | null,
    projectLink: string | null,
    reportLink: string | null,
    tags: string[] | null
  ) {
    this.title = title;
    this.subtitle = subtitle;
    this.summary = summary;
    this.infoPoints = infoPoints;
    this.description = description;
    this.image = image;
    this.videoLink = videoLink;
    this.mediaItems = mediaItems;
    this.projectLink = projectLink;
    this.reportLink = reportLink;
    this.tags = tags;
  }
}
